







































































import Component from 'vue-class-component';
import Vue from 'vue';
import { ElForm } from 'element-ui/types/form';

@Component
export default class AdminAdd extends Vue {
    public form = {};
    public rules = {};

    public created() {
        this.init();

        Object.keys(this.form).forEach(k => {
            if (k !== 'sex' && k !== 'level') {
                const rules: any[] = [
                    {
                        required: true,
                        message: '必填项不能为空',
                        trigger: 'change'
                    }
                ];
                if (k === 'phone') {
                    rules.push({
                        pattern: /^1[3456789]\d{9}$/,
                        message: '无效电话号',
                        trigger: 'change'
                    });
                }

                (<any>this.rules)[k] = rules;
            }
        });
    }

    public onSubmit() {
        const form = <ElForm>this.$refs.form;
        form.validate((valid: boolean) => {
            if (valid) {
                const now = Date.now();
                const data = Object.assign({}, this.form, {
                    createTime: now,
                    updateTime: now,
                    role: 'string' // 权限
                });

                this.$http
                    .post('/api/admin/addAdmin', data, {
                        'Content-Type': 'application/json'
                    })
                    .then(() => {
                        this.$message.success('添加成功');
                        this.init();
                    })
                    .catch(console.log);
            }
        });
    }

    public reset() {
        (<ElForm>this.$refs.form).resetFields();
        this.init();
    }

    private init() {
        this.form = {
            adminName: '',
            userName: '',
            password: '',
            sex: 1,
            department: '',
            job: '',
            level: 'T1',
            phone: '',
            workNo: ''
        };
    }
}
